import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'filtrarpeso'
})
export class FiltrarpesoPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    value = value.split("_").join(" ");
    return value;
  }
}



