import { Component, OnInit } from '@angular/core';
import {DatosUfcService} from '../datos-ufc.service';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {
  public listaNews: Array<any> = [];
  public encima = false;

  constructor(private DatosUfcService: DatosUfcService) { }

  ngOnInit() {
    this.getNews();
  }

  getNews() {
    this.DatosUfcService.noticias().subscribe((data) => {
      this.listaNews = data;
    })
  }
  cambiarVerdadero(noticia){
    noticia.encima = true;
  }
  cambiarFalso(noticia){
    noticia.encima = false;
  }

  updateUrl(evento){
    evento.target.src = "http://imagec.ufc.com/http%253A%252F%252Fmedia.ufc.tv%252F220%252FUFClogo.jpg?mw300-mh300-tc1";
  }
}



