import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtrarfecha'
})
export class FiltrarfechaPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    return value.substr(0, 10);
  }

}



