import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'buscarluchador'
})
export class BuscarluchadorPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let filtro = args;
    let ret = value.filter((luchador) => {
      return luchador.first_name.toLowerCase().includes(filtro.toLowerCase()) || luchador.last_name.toLowerCase().includes(filtro.toLowerCase());
    });
    return ret;
  }

}



