import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {DatosUfcService} from '../datos-ufc.service';

@Component({
  selector: 'app-cabecera',
  templateUrl: './cabecera.component.html',
  styleUrls: ['./cabecera.component.css']
})
export class CabeceraComponent implements OnInit {

  constructor(private router: Router, private DatosUfcService: DatosUfcService) { }

  ngOnInit() {
  }

  cambioFighters(){
    this.DatosUfcService.controlarBread = 'figthers';
  }
  cambioNews(){
    this.DatosUfcService.controlarBread = 'news';
  }
  cambioEvents(){
    this.DatosUfcService.controlarBread = 'events';
  }
  cambioHome(){
    this.DatosUfcService.controlarBread = 'home';
  }

}



