import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtrartitulo'
})
export class FiltrartituloPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    value = value.split("-").join(" ");
    return value;
  }

}



