import {Component, OnInit} from '@angular/core';
import {DatosUfcService} from '../datos-ufc.service';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {
  public listaCampeones: Array<any> = [];
  public listaEventos: Array<any> = [];

  constructor(private DatosUfcService: DatosUfcService) {
  }

  ngOnInit() {
    this.getCurrent();
    this.getEvents();
  }

  getEvents() {
    this.DatosUfcService.eventos().subscribe((data) => {
      this.listaEventos = data
    })
  }

  getCurrent() {
    this.DatosUfcService.currluchadores().subscribe((data) => {
      this.listaCampeones = data;
    })
  }

  updateUrl(evento) {
    evento.target.src = "http://imagec.ufc.com/http%253A%252F%252Fmedia.ufc.tv%252Ffeatures%252F019907_WEB_EventPlaceholderRebrand_PPV.jpg?-mw500-mh500-tc1";
  }
}




