import {Component, OnInit} from '@angular/core';
import {DatosUfcService} from '../datos-ufc.service';
import {Router} from "@angular/router";

@Component({
  selector: 'app-fighters',
  templateUrl: './fighters.component.html',
  styleUrls: ['./fighters.component.css']
})
export class FightersComponent implements OnInit {
  public listaLuchadores: Array<any> = [];
  public busqueda;

  constructor(private router: Router, private DatosUfcService: DatosUfcService) {
  }

  ngOnInit() {
    this.getFighters();
  }

  getFighters() {
    this.DatosUfcService.luchadores().subscribe((data) => {
      this.listaLuchadores = data;
    })
  }
  abrir(objeto){
    objeto.muestra = true;
  }
}



