import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {AppComponent} from './app.component';
import {CabeceraComponent} from './cabecera/cabecera.component';
import {InicioComponent} from './inicio/inicio.component';
import {FightersComponent} from './fighters/fighters.component';
import {NewsComponent} from './news/news.component';
import {FooterComponent} from './footer/footer.component';
import {DatosUfcService} from './datos-ufc.service';
import {HttpClientModule} from '@angular/common/http';
import {EventsComponent} from './events/events.component';
import {RouterModule, Routes} from "@angular/router";
import {AleatorioPipe} from './aleatorio.pipe';
import {FiltrarfechaPipe} from './filtrarfecha.pipe';
import {FiltrarpesoPipe} from './filtrarpeso.pipe';
import {FormsModule} from "@angular/forms";
import { ErrorComponent } from './error/error.component';
import { FiltrartituloPipe } from './filtrartitulo.pipe';
import { BuscarluchadorPipe } from './buscarluchador.pipe';

const routes: Routes = [
  {path: 'home', component: InicioComponent},
  {path: 'fighters', component: FightersComponent},
  {path: 'news', component: NewsComponent},
  {path: 'events', component: EventsComponent},
  {path: '', redirectTo: '/home', pathMatch: 'full'},
  {path: '**', component: ErrorComponent},
];

@NgModule({
  declarations: [
    AppComponent,
    CabeceraComponent,
    InicioComponent,
    FightersComponent,
    NewsComponent,
    FooterComponent,
    EventsComponent,
    AleatorioPipe,
    FiltrarfechaPipe,
    FiltrarpesoPipe,
    ErrorComponent,
    FiltrartituloPipe,
    BuscarluchadorPipe,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(routes)
  ],
  providers: [DatosUfcService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



